// Implementation with Node's http libraries
export const get = function(url) {
    // return new pending promise
    return new Promise((resolve, reject) => {
        // select http or https module, depending on reqested url
        const lib = url.startsWith('https') ? require('https') : require('http');
        const request = lib.get(url, (response) => {
            // handle http errors
            if (response.statusCode < 200 || response.statusCode > 299) {
                reject(new Error('Failed to load page, status code: ' + response.statusCode));
            }
            // temporary data holder
            const body = [];
            // on every content chunk, push it to the data array
            response.on('data', (chunk) => body.push(chunk));
            // we are done, resolve promise with those joined chunks
            response.on('end', () => resolve(body.join('')));
        });
        // handle connection errors of the request
        request.on('error', (err) => reject(err))
    })
};

// Implementation with XMLHttpRequest
export const get2 = function(url) {
    // return new pending promise
    return new Promise((resolve, reject) => {
        const request = new XMLHttpRequest();
        let data = [];
        request.onreadystatechange = () => {
            if (request.readyState == XMLHttpRequest.DONE && request.status == 200) {
                console.log('request done!');
                console.log(request.response);
                resolve(request.response);
            } else if (request.readyState == XMLHttpRequest.DONE &&
                       (request.status < 200 || request.status > 299)) {
                reject(new Error('Failed to load page - status: ' + request.status));
            }
        };

        request.open('GET', url);
        request.send(null);
    })
};


var HttpClient = function() {
    this.get = function(url, callback) {
        let request = new XMLHttpRequest();
        request.onreadystatechange = function() {
            if (request.readyState == 4 && request.status == 200) {
                callback(request.responseText);
            } else {
                console.log('--------- Error! ---------');
                console.log(request);
            }
        }
        request.open('GET', url, true);
        request.send(null);
    }
};



